import React, { useEffect, useState } from "react";
import { Button, Form } from "react-bootstrap";
import styles from "./SearchBar.module.scss";
import { Autocomplete, TextField } from "@mui/material";
import { useDispatch } from "react-redux";
import { resetWeather, setWeather } from "../../features/weather/WeatherSlice";
import PositionSVG from "../SVGs/PositionSVG";

export default function SearchBar() {
    const GEO_API_KEY = process.env.REACT_APP_GEO_API_KEY;
    const OPENWEATHER_API_KEY = process.env.REACT_APP_OPENWEATHER_API_KEY;
    const dispatch = useDispatch();
    const [cities, setCities] = useState([]);
    const [unity] = useState("metric");
    const [geoLocation, setGeoLocation] = useState(undefined);
    const [isCurrentLocation, setIsCurrentLocation] = useState(false);

    const getGeoLocation = () => {
        navigator.geolocation.getCurrentPosition((positon) => {
            setIsCurrentLocation(true);
            setGeoLocation({
                lon: positon.coords.longitude,
                lat: positon.coords.latitude,
            });
        });
    };

    useEffect(() => {
        getGeoLocation();
    }, []);

    // Will triggered when geolocation values changes!
    useEffect(() => {
        getData();
    }, [geoLocation]);

    const handleInputChange = (e) => {
        const { value } = e.currentTarget;
        fetch(
            `https://api.geoapify.com/v1/geocode/autocomplete?text=${value}&type=city&format=json&apiKey=${GEO_API_KEY}`
        )
            .then((response) => response.json())
            .then((json) =>
                setCities(
                    json.results.map((data) => {
                        const { lat, lon, city, country, formatted } = data;
                        return { lat, lon, city, country, formatted };
                    })
                )
            );
    };

    const getData = () => {
        if (geoLocation) {
            fetch(
                `https://api.openweathermap.org/data/2.5/weather?units=${unity}&lat=${geoLocation.lat}&lon=${geoLocation.lon}&appid=${OPENWEATHER_API_KEY}`
            )
                .then((response) => response.json())
                .then((json) => {
                    const { clouds, main, name, sys, weather, wind } = json;
                    dispatch(setWeather({ clouds, main, name, sys, weather, wind }));
                });
        }
    };

    const handleAutocompleteSelect = (e, value) => {
        if (value != null) {
            const { lat, lon } = value;
            setIsCurrentLocation(false);
            setGeoLocation({
                lat,
                lon,
            });
        } else {
            dispatch(resetWeather());
        }
    };

    return (
        <>
            <Form>
                <Form.Group className={styles.searchContainer}>
                    <Autocomplete
                        className={styles.searchInput}
                        clearOnBlur={false}
                        onChange={handleAutocompleteSelect}
                        getOptionLabel={(option) => option.formatted}
                        renderInput={(params) => (
                            <TextField
                                onChange={handleInputChange}
                                {...params}
                                label={"Enter your city ..."}
                            />
                        )}
                        options={cities}
                    />
                    <Button disabled={geoLocation === undefined || isCurrentLocation === true} 
                        variant="contained" 
                        onClick={() => getGeoLocation()}>
                        <PositionSVG color={'#fff'} />
                    </Button>
                </Form.Group>
            </Form>
        </>
    );
}
