import React from 'react'
import styles from './Weather.module.scss'
import { Card } from 'react-bootstrap'
import PositionSVG from '../SVGs/PositionSVG'
import Thermometer from '../SVGs/Thermometer'
import Time from '../SVGs/Time'
import Wind from '../SVGs/Wind'
import Sunny from '../SVGs/Sunny'
import { useSelector } from 'react-redux'
import Moment from 'react-moment'
import Speedometer from '../SVGs/Speedometer'
import Humidity from '../SVGs/Humidity'
import { Switch } from '@mui/material'
import { motion } from 'motion/react'

export default function Weather() {
  const weather = useSelector( ({weather}) => weather )
  
  return (
    <>
        <Card className={styles.container}>
          { weather.isLoaded ? 
            <Card.Body>
              <Card.Title>
                {weather.name}, {weather.sys?.country} <PositionSVG color={'rgba(256,256,256,0.7)'} width='25px' height='25px' />
                <div className={styles.date}>
                  <div>
                    <Moment format={'llll'} />
                  </div>
                  <div><Time /></div>
                </div>
              </Card.Title>
              <Switch />
              <Card.Text as={'div'} className={styles.weather_infos}>
                  <div>
                    <motion.img
                    initial={{
                      opacity: 0,
                      y: [0, 0, 0]
                    }}
                    animate={{ 
                      opacity: 1,
                      y: [0, -20 , 0]
                    }}
                    transition={{
                      y: {repeat: 'loop', duration: 5, ease: 'easeInOut'}
                    }}
                    src={`https://openweathermap.org/img/wn/${weather.weather.icon}@2x.png`} alt="" />
                  </div>
                  <div className={styles.temperature}>
                    <div>{weather.main.feels_like} C</div>
                    <div><Thermometer color='#ff0' width='45px' height='45px' /></div>
                  </div>
                  <div>
                    {'Good Morning'} {weather.name}
                    <div className={styles.separator}></div>
                  </div>
                  <div className={styles.infos}>
                    <div className={styles.border_right}>
                      <div><Sunny /></div>
                      <div>{'Sunrise'}</div>
                      <div>
                        <Moment unix={true} format='hh:mm'>
                          {weather.sys?.sunrise}
                        </Moment>
                      </div>
                    </div>
                    <div className={styles.border_right}>
                      <div><Wind /></div>
                      <div>{'Wind'}</div>
                      <div>{weather.wind?.speed }m/s</div>
                    </div>
                    <div className={styles.border_right}>
                      <div><Speedometer /></div>
                      <div>{'Speedometer'}</div>
                      <div>...</div>
                    </div>
                    <div className={styles.border_right}>
                      <div><Humidity /></div>
                      <div>{'Humidity'}</div>
                      <div>{weather.main.humidity}</div>
                    </div>
                    <div>
                      <div><Thermometer /></div>
                      <div>{'Temp'}</div>
                      <div>{weather.main.temp_max} C</div>
                    </div>
                  </div>
              </Card.Text>
            </Card.Body>
          :
            <Card.Body>
              <Card.Title>
                Please, chose your city!
              </Card.Title>
            </Card.Body>
          }
        </Card>
    </>
  )
}
