import { createSlice } from "@reduxjs/toolkit"

const weatherState = {
    clouds: undefined,
    main: {
        feels_linke: undefined
    },
    name: undefined,
    sys: {
        country: undefined
    },
    weather: undefined,
    wind: {
        speed: undefined
    },
    isLoaded: false
}

const WeatherSlice = createSlice({
    name: 'weather',
    initialState: weatherState,
    reducers: {
        setWeather: (state, action) => {
            const {clouds, main, name, sys, weather, wind} = action.payload
            state.clouds = clouds
            state.main = main
            state.name = name
            state.sys = sys
            state.weather = weather[0]
            state.wind = wind
            state.isLoaded = true
        },
        resetWeather: (state) => {
            state.isLoaded = false
        }
    }
})

export const {setWeather, resetWeather} = WeatherSlice.actions
export default WeatherSlice.reducer
